<script>
import { getCouponInfoSubGroupInfo } from '@/api/marketManage/coupon'
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      active: "", //绑定样式
      deleteBtn:false
    };
  },
  created(){
    if(this.itemData.couponIds.length > 0) this.getData()
  },
  methods: {
    getData(){
      console.log('优惠券组件数据回显',this.itemData)
      let arr = []
      this.itemData.couponIds.forEach(e => {
        arr.push(e)
      });
      getCouponInfoSubGroupInfo(arr.join(',')).then(res=>{
        console.log('优惠券回显',res)
        if(res.code == 200){
          let arr = res.data
          this.itemData.coupons = arr
        }
      })
    },
    // 移入事件
    mouseOver() {
      this.active = "active";
      this.deleteBtn=true;
    },
    // 移出事件
    mouseLeave() {
      this.active = "";
      this.deleteBtn=false;
    },
    //删除当前组件盒子事件
    deleteBox(box) {
      this.$emit("deleteBox", box);
    },
  }
};
</script>

